	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Half Empty or Half Dead?","date":"07 Aug 2005","slug":"/2005-08-07-half-empty-or-half-dead/","tags":"Y2005"},"html":"<p>BBC: <a href=\"http://news.bbc.co.uk/2/hi/europe/4127636.stm\" title=\"BBC NEWS | Europe | Plane crash off Sicily kills 13\">Plane crash off Sicily kills 13</a></p>\n<p>CNN: <a href=\"http://www.cnn.com/2005/WORLD/europe/08/06/sicily.plane/index.html\" title=\"CNN.com - 23 saved after plane crashes in Mediterranean - Aug 6, 2005\">23 saved after plane crashes in Mediterranean</a></p>"},"next":{"fields":{"title":"Billboards Without Faces","date":"07 Aug 2005","slug":"/2005-08-07-billboards-without-faces/"}},"previous":{"fields":{"title":"Bad Tickets","date":"09 Aug 2005","slug":"/2005-08-09-bad-tickets/"}}},{"node":{"fields":{"title":"Billboards Without Faces","date":"07 Aug 2005","slug":"/2005-08-07-billboards-without-faces/","tags":"Y2005"},"html":"<p><a href=\"http://www.flickr.com/photos/eris23/30363984/\" title=\"photo sharing\"><img src=\"http://photos21.flickr.com/30363984_b022a27702_m.jpg\" alt=\"\" style=\"border: solid 2px #000000;\" /></a><br />\n<span style=\"font-size: 0.9em; margin-top: 0px;\">Originally uploaded by <a href=\"http://www.flickr.com/people/eris23/\">eris23</a>.</span></p>\n<p>The campaign season started while we were away, and this billboard now stands in front of our local supermarket. \"We stand for the courage to wage peace. But what do the others stand for?\"</p>\n<p>I'm not exactly sure which war the SPD is claiming to have prevented. If they mean Iraq, that certainly didn't work, and sticking one's head in the sand and pretending a problem doesn't exist doesn't exactly require courage. What they really mean is \"We stood up to Bush\", implying that Angela Merkel wouldn't have. It's a whiff of anti-Americanism, and it will be interesting to see if the SPD takes it any further. </p>\n<p>The other interesting thing is the ruling party chose not to use a photo of their candidate, even though Gerhard Schröder is personally more popular than Merkel. Which party is the challenger here? Then again, it's a strange election, where neither party wants to be the incumbant.</p>\n<p>This is one of a series of \"What do the others stand for?\" slogans... other issues the SPD \"stands for\" include <a href=\"http://www.flickr.com/photos/sebastian-wendler/31724237/in/pool-wahl05/\">job protection</a>, <a href=\"http://www.flickr.com/photos/ix/31256013/in/pool-wahl05/\">reform</a>,  <a href=\"http://www.flickr.com/photos/sebastian-wendler/31131636/in/pool-wahl05/\">modern family policy</a>, and <a href=\"http://www.flickr.com/photos/sebastian-wendler/31130961/in/pool-wahl05/\">social justice</a>. More billboards are at <a href=\"http://www.flickr.com/groups/wahl05/\">the Flickr group Wahl05</a>. </p>"},"next":{"fields":{"title":"Bomb. bay. doors. opened.","date":"06 Aug 2005","slug":"/2005-08-06-bomb-bay-doors-opened/"}},"previous":{"fields":{"title":"Half Empty or Half Dead?","date":"07 Aug 2005","slug":"/2005-08-07-half-empty-or-half-dead/"}}},{"node":{"fields":{"title":"Bomb. bay. doors. opened.","date":"06 Aug 2005","slug":"/2005-08-06-bomb-bay-doors-opened/","tags":"Y2005"},"html":"<p><a href=\"http://www.2020hindsight.org/2005/08/05/1945-suspense/\" title=\"2020 Hindsight &raquo; 1945: Suspense\">Bomb. bay. doors. opened.</a> Susan pauses her account of the dropping of the atomic bomb on Hiroshima for 43 seconds.</p>"},"next":{"fields":{"title":"Vacation Impressions","date":"05 Aug 2005","slug":"/2005-08-05-vacation-impressions/"}},"previous":{"fields":{"title":"Billboards Without Faces","date":"07 Aug 2005","slug":"/2005-08-07-billboards-without-faces/"}}}],"pathPrefix":"","first":false,"last":false,"index":291,"pageCount":1101}}

